.cancel {
  background-color: #E3E4E5;
  color: #686969;

  border: none;
  border-radius: 20px;
  padding: 20px 30px;
  margin-top: 10px;
  
  font-family: 'TinkoffSans';
  font-weight: 400;
  font-size: 24px;

  cursor: pointer;

  transition: background-color 0.2s linear;
}

.cancel:hover {
  background-color: #C4C8CD;
}

.button {
  background-color: #FFDD2D;
  color: black;

  border: none;
  border-radius: 20px;
  padding: 20px 30px;
  margin-top: 10px;
  
  font-family: 'TinkoffSans';
  font-weight: 400;
  font-size: 24px;

  cursor: pointer;

  transition: background-color 0.2s linear;
}

.button:hover {
  background-color: #bda52a;
}

@media screen and (max-width: 700px) {
  .button {
    border-radius: 10px;
    padding: max(10px, 3.5vw) max(15px, 5vw);

    font-size: max(16px, 3.5vw);
  }

  .cancel {
    border-radius: 10px;
    padding: max(10px, 3.5vw) max(15px, 5vw);

    font-size: max(16px, 3.5vw);
  }
}