.form {
  width: 630px;
  text-align: center;

  border-radius: 20px;
  padding: 50px;


  background-color: #fff;
}

.err {
  color: #f00;

  margin-top: 10px;
  font-size: 16px;

  height: 20px;
}

@media screen and (max-width: 1350px) {
  .form {
    margin: auto;

    margin-top: 30px;
  }
}

@media screen and (max-width: 700px) {
  .form {
    width: 100%;

    padding: 8vw;
  }
}